.portfolio-section {

  .container-fluid {
    padding-left: 0;
    padding-right: 0;
      @media (min-width: $screen-sm) {
        padding-left: 15px;
        padding-right: 15px;
      }
  }
  &__item {
    margin-top: 20px;

  }


  &__item-caption {
    display: none;
      @media (min-width: $screen-sm) {
        display: block;
      }
  }
}
